<template>
    <div class="dashboard-wrap">
        <div>
            <h3 class="dashboard-title">Details</h3>
            <div class="" style="display: flex; justify-content: center">
                <v-card style="width: 90%;">
                    <div style="padding:20px;">
                        <v-container>
                            <v-row>
                                <v-col cols="12" md="6">
                                    <img :src="hostel.img" style="width: 100%;" alt="">
                                </v-col>
                                <v-col cols="12" md="6">
                                    <div class="detail-wrap">
                                        <div class="content_wrap">
                                            <h1 class="head">
                                            {{hostel.hall_Name.toUpperCase()}} HOSTEL
                                            </h1>
                                            <div class="subHead">
                                            Legoland & Water Park Combo {{hostel.location}}
                                            </div>
                                            <div class="primary_content">
                                            <div class="rating">4.0*</div>
                                            <div class="review">(0 Reviews)</div>
                                            <s class="old_price">$150</s>
                                            </div>
                                            <div class="primary_content">
                                                <div class="discount">Save upto 50% off</div>
                                                <div class="new_price">${{hostel.fees}}</div>
                                            </div>
                                            <div style="width: 100%;">
                                                <h3 class="detail-head">Details</h3>
                                                <div class="detail-content" style="display: flex; flex-direction: column; padding: 10px;">
                                                    <div class="primary-content" style="display: flex; flex-direction: row;">
                                                        <div class="cont">Hostel Code</div>
                                                        <div class="cont_val">{{hostel.hall_Code}}</div>
                                                    </div>
                                                    <div class="primary-content" style="display: flex; flex-direction: row;">
                                                        <div class="cont">Number of Room</div>
                                                        <div class="cont_val">{{hostel.room_No}}</div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div>
                                                <v-btn @click.prevent="cancelBooking()" v-if="user.bed" style="width: 100%; color: #fff" color="red">
                                                    Cancel Active Booking
                                                </v-btn>
                                                <v-dialog v-else v-model="dialog" max-width="300px">
                                                    <template v-slot:activator="{ on }">
                                                        <v-btn v-on="on" style="width: 100%; color: #fff" color="rgb(43, 94, 86)">
                                                            BOOK NOW
                                                        </v-btn>
                                                    </template>
                                                            <v-card>
                                                        <v-form @submit.prevent="bookHostel()">
                                                            <v-container>
                                                                <v-row  style="padding : 0px 12px;">
                                                                        <v-text-field
                                                                        v-model="contact_No"
                                                                        :error-messages="contact_NoErrors"
                                                                        label="Contact No."
                                                                        outlined
                                                                        dense
                                                                        required
                                                                        @input="$v.contact_No.$touch()"
                                                                        @blur="$v.contact_No.$touch()"
                                                                        ></v-text-field>
                                                                </v-row>
                                                                <v-row>
                                                                    <v-col cols="12" style="padding : 0px 12px;">
                                                                        <v-text-field
                                                                        v-model="guardian_Name"
                                                                        :error-messages="guardian_NameErrors"
                                                                        label="Guardian Name"
                                                                        outlined
                                                                        dense
                                                                        required
                                                                        @input="$v.guardian_Name.$touch()"
                                                                        @blur="$v.guardian_Name.$touch()"
                                                                        ></v-text-field>
                                                                    </v-col>
                                                                </v-row>
                                                                <v-row>
                                                                    <v-col cols="12" style="padding : 0px 12px;">
                                                                        <v-text-field
                                                                        v-model="guardian_No"
                                                                        :error-messages="guardian_NoErrors"
                                                                        label="Guardian No."
                                                                        outlined
                                                                        dense
                                                                        required
                                                                        @input="$v.guardian_No.$touch()"
                                                                        @blur="$v.guardian_No.$touch()"
                                                                        ></v-text-field>
                                                                    </v-col>
                                                                </v-row>
                                                                <v-row>
                                                                    <v-col cols="12" style="padding : 0px 12px;">
                                                                        <v-text-field
                                                                        v-model="duration"
                                                                        :error-messages="durationErrors"
                                                                        label="Duration"
                                                                        required
                                                                        outlined
                                                                        dense
                                                                        @input="$v.duration.$touch()"
                                                                        @blur="$v.duration.$touch()"
                                                                        ></v-text-field>
                                                                    </v-col>
                                                                </v-row>                               
                                                                <v-row>
                                                                    <v-col cols="12" style="padding : 0px 12px;">
                                                                        <v-btn type="submit" color="rgb(43, 94, 86)" style="width: 100%; color: #FFF" @click="submit">
                                                                            Book
                                                                        </v-btn>
                                                                    </v-col>
                                                                </v-row>
                                                            </v-container>
                                                        </v-form>
                                                    </v-card>
                                                </v-dialog>
                                            </div>
                                        </div>
                                    </div>
                                </v-col>
                            </v-row>
                            <div class="r">

                            </div>
                        </v-container>
                    </div>
                </v-card>
            </div>
        </div>
    </div>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { required } from 'vuelidate/lib/validators'
  import {EventBus} from "@/main"

export default {
    name: "AddHostel",
    mixins: [validationMixin],
    validations: {
      contact_No: {required},
      guardian_No: {required},
      guardian_Name: {required},
      duration: {required}
    },
    data : ()=> {
        return {
            user:"",
            hostel: "",
            contact_No: "",
            guardian_Name: "",
            guardian_No: "",
            duration: "",
            err: "",
            detail: "",
            dialog: false
        }
    },
     computed: {
      contact_NoErrors () {
        const errors = []
        if (!this.$v.contact_No.$dirty) return errors
        !this.$v.contact_No.required && errors.push('contact No is required.')
        return errors
      },
      guardian_NameErrors () {
        const errors = []
        if (!this.$v.guardian_Name.$dirty) return errors
        !this.$v.guardian_Name.required && errors.push('Guardian Name is required.')
        return errors
      },
        guardian_NoErrors () {
        const errors = []
        if (!this.$v.guardian_No.$dirty) return errors
        !this.$v.guardian_No.required && errors.push('Guardian No is required.')
        return errors
      },
      durationErrors () {
        const errors = []
        if (!this.$v.duration.$dirty) return errors
        !this.$v.duration.required && errors.push('Duration is required.')
        return errors
      },
    },
    mounted() {
        this.fetchHostelDetail(),
        this.getAuthUser()
    },
    methods: {
      submit () {
        this.$v.$touch()
        console.log({
            hostel: this.hostel
        })
      },
      async fetchHostelDetail () {
        const id = this.$route.params.id
        const response = await this.$http.get(`/api/booking/hostelDetail/${id}`)
        this.hostel = response.data.payload[0]
        console.log(this.hostel)
      },
      async bookHostel () {
          const id = this.$route.params.id
          const response = await this.$http.post("/api/booking/create", {
              hostelID : id,
              guardian_No: this.guardian_No,
              guardian_Name: this.guardian_Name,
              contact_No: this.contact_No,
          })
          EventBus.$emit("Success", {
              msg : response.data.booking
          })
          this.$router.push({
              path: "/"
          })
      },
      async getAuthUser() {
            const response = await this.$http.get("/api/user/getAuthUser")
            this.user = response.data.user[0]
      },
      async cancelBooking () {
          const response = await this.$http.get("/api/booking/cancel")
          this.getAuthUser()
          this.msg = response.data.msg
      }
    },
    components: {
        // Drawer
    }
}
</script>

<style scoped>
    .dashboard-wrap{
        margin: 10px 10px;
    }
    .dashboard-title{
        font-size: 24px;
        padding: 0 0 20px 50px;
    }
    .content_wrap {
    padding: 0.5rem 1rem;
    color: black;
    }
    .head {
        font-size: 18px;
        color: black;
        font-weight: 500;
        padding: 0 0 0.7rem 0;
    }
    .subHead {
        color: #535252;
        font-size: 13px;
        padding: 0 0 1.3rem 0;
    }
    .primary_content {
        display: flex;
        flex-direction: row;
        justify-content: stretch;
        align-items: center;
        padding: 0 0 0.5rem 0;
    }
    .rating {
        background-color: orangered;
        border-radius: 1rem;
        font-size: 12px;
        font-weight: 700;
        margin-right: 0.7rem;
        width: 4rem;
        padding: 2px;
        text-align: center;
        color: #fff;
    }
    .review {
        font-size: 12px;
        color: lightgray;
        width: 100%;
    }
    .old_price {
        font-size: 13px;
        color: #888383;
    }
    .discount {
        color: #26a2ca;
        font-size: 12px;
        width: 100%;
    }
    .new_price {
        font-size: 20px;
        color: #464242;
        font-weight: 50;
    }
    .views {
        color: black;
        padding: 8px;
        font-size: 13px;
    }
    .detail-head{
        font-size: 18px;
        color: black;
        font-weight: 500;
    }
    .detail-content{
        display: flex;
        flex-direction: column;
    }
    .primary-detail{
        display: flex;
        flex-direction: row;
        color: red;
    }
    .cont{
        color: black;
        font-size: 13px;
        width: 100%;
    }
    .cont_val{
        font-size: 17px;
        color: #464242;
        font-weight: 50;
    }
</style>